import requests from './httpService';

const OrderServices = {
  getAllOrders(body) {
    return requests.get('/orders?admin=1', body);
  },

  getOrderByUser(id, body) {
    return requests.get(`/orders/user/${id}?admin=1`, body);
  },

  getOrderById(id, body) {
    return requests.get(`/admin/orders/${id}?admin=1`, body);
  },

  updateOrder(id, body, headers) {
    return requests.put(`/orders/${id}?admin=1`, body);
  },

  deleteOrder(id) {
    return requests.delete(`/orders/${id}?admin=1`);
  },

  getAllRentalsOrders(body) {
    return requests.get('/rentals?admin=1', body);
  },
};

export default OrderServices;
